.toast {
  margin-top: 24px;
  color: #7189FF; }

@-webkit-keyframes FadeAnimation {
  0% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

@keyframes FadeAnimation {
  0% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

@-webkit-keyframes FadeAnimation2 {
  0% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

@keyframes FadeAnimation2 {
  0% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

@-webkit-keyframes pop {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  35% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  85% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes pop {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  35% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  85% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@-webkit-keyframes shadow-drop-bottom {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0); }
  100% {
    -webkit-box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35);
    box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35); } }

@keyframes shadow-drop-bottom {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0); }
  100% {
    -webkit-box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35);
    box-shadow: 0 12px 20px -12px rgba(0, 0, 0, 0.35); } }

@-webkit-keyframes tracking-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    -webkit-transform: translateZ(-700px);
    transform: translateZ(-700px);
    opacity: 0; }
  40% {
    opacity: 0.6; }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1; } }

@keyframes tracking-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    -webkit-transform: translateZ(-700px);
    transform: translateZ(-700px);
    opacity: 0; }
  40% {
    opacity: 0.6; }
  100% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1; } }

@-webkit-keyframes tracking-out-contract {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    letter-spacing: -0.5em;
    opacity: 0; } }

@keyframes tracking-out-contract {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    letter-spacing: -0.5em;
    opacity: 0; } }

.fadeOut {
  opacity: 1;
  visibility: visible;
  -webkit-animation: FadeAnimation 1.5s ease-in 1.25s forwards;
  animation: FadeAnimation 1.5s ease-in 1.25s forwards; }

.fadeOut2 {
  opacity: 1;
  visibility: visible;
  -webkit-animation: FadeAnimation2 1.5s ease-in 1.25s forwards;
  animation: FadeAnimation2 1.5s ease-in 1.25s forwards; }

.profile {
  width: 420px;
  margin: 160px auto 30px;
  padding: 16px;
  position: relative;
  /* For the submit button positioning */
  z-index: 1;
  /* Styles */
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), 0 3px 7px rgba(0, 0, 0, 0.3), inset 0 1px white, inset 0 -3px 2px rgba(0, 0, 0, 0.25);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), 0 3px 7px rgba(0, 0, 0, 0.3), inset 0 1px white, inset 0 -3px 2px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  background: -webkit-gradient(linear, left top, left bottom, from("empty"), color-stop(10%, #ffffff));
  background: -webkit-linear-gradient("empty", #ffffff 10%);
  background: -o-linear-gradient("empty", #ffffff 10%);
  background: linear-gradient("empty", #ffffff 10%); }
  .profile .profileDescription {
    text-align: center;
    font-size: 24px;
    color: #7189FF;
    margin-bottom: 32px;
    font-weight: 700; }
  .profile div {
    text-align: center; }
  .profile .left-side {
    text-align: left;
    line-height: 2; }
    .profile .left-side .pencil {
      position: absolute;
      right: 0;
      margin-right: 12px;
      cursor: pointer; }
  .profile .right-side {
    font-weight: 600;
    position: relative;
    margin-left: 16px; }
  .profile .input {
    /* Fonts and Text */
    font-family: "Lato", Calibri, Arial, sans-serif;
    font-size: 13px;
    font-weight: 400;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
    /* Size and position */
    width: 80%;
    padding: 10px 18px 10px 45px;
    margin-top: 8px;
    margin-bottom: 24px;
    /* Styles */
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1), inset 0 3px 2px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1), inset 0 3px 2px rgba(0, 0, 0, 0.1);
    border: none;
    /* Remove the default border */
    border-radius: 5px;
    background: #f9f9f9;
    color: #777;
    -webkit-transition: color 0.3s ease-out;
    -o-transition: color 0.3s ease-out;
    transition: color 0.3s ease-out; }
    .profile .input:hover {
      background-color: #ececec; }
  .profile .input.textArea {
    text-align: left;
    height: 64px; }
  .profile button {
    /* Size and position */
    width: 100%;
    height: 100%;
    margin-top: 12px;
    margin-bottom: 8px;
    /* Icon styles */
    font-size: 1.2em;
    line-height: 1.75;
    color: white;
    /* Styles */
    border: none;
    border-radius: 5px;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(113, 137, 255, 0.9)), to(#7189FF));
    background: -webkit-linear-gradient(rgba(113, 137, 255, 0.9), #7189FF);
    background: -o-linear-gradient(rgba(113, 137, 255, 0.9), #7189FF);
    background: linear-gradient(rgba(113, 137, 255, 0.9), #7189FF);
    -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.3), inset 0 3px 2px rgba(255, 255, 255, 0.3), inset 0 -3px 2px rgba(0, 0, 0, 0.3);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.3), inset 0 3px 2px rgba(255, 255, 255, 0.3), inset 0 -3px 2px rgba(0, 0, 0, 0.3);
    cursor: pointer; }
    .profile button:hover {
      color: #bbb; }
  .profile a {
    font-family: "Lato", Calibri, Arial, sans-serif;
    font-size: 15px; }
